@use "00-base/configure" as *;

.ma__location-icons {
  margin-bottom: -30px;

  &__items {
    display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
  }

  &__item {
    text-align: center;
    margin-bottom: 30px;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: 80px;
    min-width: 80px;
  }

  &__icon {
    height: 40px;
    margin-bottom: 18px;
    text-align: center;

    & > svg {
      height: 100%;
      width: 100%;
      fill: var(--mf-c-primary-alt);
    }
  }

  &__name {
    font-size: $fonts-xsmall;
    margin: 0 auto;
    letter-spacing: $letter-spacing-large;
    line-height: 1.3;
    max-width: 6rem;
    text-transform: uppercase;
    color: var(--mf-c-font-medium);
    font-weight: $fonts-bold;
  }
}
