@use "00-base/configure" as *;

.ma__press-teaser {

  @include clearfix();

  &--image {
    display: flex;
      flex-wrap: wrap;
  }

  & + & {
    border-top-width: 1px;
    border-top-style: solid;
    margin-top: 30px;
    padding-top: 30px;

    @media ($bp-large-min) {
      margin-top: 45px;
      padding-top: 45px;
    }

    .sidebar & {
      margin-top: 20px;
      padding-top: 20px;
    }
  }

  .pre-content > &,
  .post-content > &,
  .main-content--full .page-content > & {

    @include ma-container('restricted');
  }

  &__image {
    flex-shrink: 1;
    flex-grow: 0;
    flex-basis: 400px;
    font-size: 0;
    margin-right: 30px;
    margin-bottom: 10px;

    span {
      background-size: cover;
      display: block;
      padding-top: 56.25%;
      width: 100%;
    }
  }

  &__image + &__details {
    flex-basis: 300px;
    flex-shrink: 1;
    flex-grow: 1;
  }

  &__eyebrow {
    margin-bottom: 5px;

    span {
      font-size: $fonts-xsmall;
      letter-spacing: $letter-spacing-large;
      padding: 5px 7px;
      text-transform: uppercase;
    }
  }

  &__title {
    font-size: $fonts-xlarge;
    line-height: 1.5;
    margin-bottom: .25em;

    .ma__decorative-link {
      font-size: $fonts-xlarge;
    }
  }

  &__details {
    padding: 2px 0 4px;
  }

  &__date,
  &__org {
    font-size: $fonts-small;
    line-height: 1;
    margin-bottom: 1em;
  }

  &__date + &__org {
    border-left-style: solid;
    border-left-width: 1px;
    margin-left: .75em;
    padding-left: .75em;
  }

  &__description {
    font-size: $fonts-medium;

    @media ($bp-x-small-max) {
      display: none;
    }
  }
}

//theme

.ma__press-teaser {

  & + & {
    border-top-color: var(--mf-c-bd-divider);
  }

  &__eyebrow span {
    background-color: var(--mf-c-bg-section);
    color: var(--mf-c-font-detail);
    font-weight: $fonts-bold;
  }

  &__date,
  &__org {
    font-style: italic;
    font-weight: $fonts-bold;
  }

  &__org {
    border-left-color: var(--mf-c-bd-divider);
  }
}
