@use "00-base/configure" as *;

.ma__event-filters {
  padding-top: 30px;

  form {

    @include ma-container;
    display: flex;
      flex-wrap: wrap;
  }

  &__by-location,
  &__date-range,
  &__buttons {
    margin-bottom: 30px;
    width: 100%;

    @media ($bp-x-large-min) {
      margin-bottom: 45px;
    }
  }

  &__by-location {

    input {
      max-width: $l-max-content-width;
      width: 100%;
    }
  }

  // backward compatible with v5.6

  &__submit {

    @include ma-button-size('small');
  }
}

//theme

.ma__event-filters {
  background-color: var(--mf-c-bg-section);

  // backward compatible with v5.6

  &__submit {

    @include ma-button('solid',$c-primary);
  }
}
